:root {
    ---primary: #fff;

}

.btn {
    padding:8px 20px;
    border-radius: 2px;
    outline: none;
    border: none;
    cursor: pointer;

}

.btn--primary {
    background-color: var(--primary);
    color: black;
    border: 1px solid var(---primary);
}

.btn--outline {
    background-color: transparent;
    color: white;
    padding: 8px 20px;
    transition: all 0.3s ease-out;
}

.btn-medium {
    padding: 8px 20px;
    font-size: 20px;

}

.btn--large {
    padding: 8px 26px;
    font-size: 20px;

}

.btn--medium:hover, .btn--large:hover {
    background-color: white;
    color: black;
    transition: all 0.3s ease-out;

}